import React, { useState } from 'react';
import { Input, Checkbox } from 'antd';
import { requiredRule } from '../../../utils/validateRules';
import CrudPage from '../../../components/common/CrudPage';
import {
  massCreateStaffAccount,
  updatePassword,
  updateStatus,
} from '../../../services/userPortal';
import Switch2Value from '../../../components/common/Input/Switch2Value';
import { searchStaffAccount } from '../../../services/staffPortal';

export default () => {
  const [rowsSelected, setRowsSelected] = useState({});
  const model = [
    { field: 'staffId', name: 'Mã cán bộ', formItem: <Input /> },
    { field: 'fullName', name: 'Họ tên', formItem: <Input /> },
    { field: 'userName', name: 'Tài khoản', formItem: <Input /> },
    { field: 'userDisabled', name: 'Vô hiệu', editFormItem: <Switch2Value /> },
    { field: 'password', name: 'Mật khẩu', rules: [requiredRule], newFormItem: <Input.Password /> },
    { field: 'updatePassword', name: 'Đặt lại mật khẩu', editFormItem: <Input.Password /> },
  ];

  const config = {
    title: 'Tài khoản Giáo viên',
    serviceGetList: searchStaffAccount,
    serviceCreate: massCreateStaffAccount,
    serviceUpdate: async values => {
      console.log(values);
      if (values.updatePassword) {
        await updatePassword({ ...values, password: values.updatePassword });
      }
      await updateStatus({ ...values, disabled: values.userDisabled });
    },
    searchFormItems: ['staffId', 'fullName'],
    tableColumns: [
      {
        title: '',
        key: 'select',
        render: (text, record) => {
          if (record.userName) {
            return null;
          }
          return (
            <Checkbox
              onChange={e => {
                if (e.target.checked) {
                  setRowsSelected({ ...rowsSelected, [record.staffId]: record });
                } else {
                  const newRowsSelected = { ...rowsSelected };
                  delete rowsSelected[record.staffId];
                  setRowsSelected(newRowsSelected);
                }
              }}
            />
          );
        },
      },
      'staffId',
      'fullName',
      'userName',
      {
        title: 'Vô hiệu',
        key: 'userDisabled',
        dataIndex: 'userDisabled',
        render: text => {
          return <Checkbox checked={!!text} />;
        },
      },
    ],
    
    btnAdd: {
      props: {
        disabled: Object.keys(rowsSelected).length === 0,
      },
    },
    btnEdit: record => {
      return {
        props: {
          disabled: !record.userName,
        },
      };
    },
    saveFormItems: ['password', 'userDisabled', 'updatePassword'],
    saveFormCustomValue: values => {
      if (values.FORM_MODE === 'NEW') {
        return Object.values(rowsSelected)
          .filter(item => item)
          .map(item => {
            return {
              staffId: item.staffId,
              password: values.password,
            };
          });
      }
      return values;
    },
    saveFormOnSuccess: () => {
      setRowsSelected({});
    },
  };
  return <CrudPage model={model} config={config} />;
};



