import { request } from '../utils/requestToJs';

const prefix = '/api/organization';

export const searchStaffAccount = ({
  staffId,
  fullName,
  staffIds,
  page,
  pageSize = 10,
  orderBy,
  isPagingEnabled = true,
} = {}) =>
  request({
    prefix,
    url: `/Staff/SearchStaffAccount`,
    method: 'POST',
    params: {},
    data: { staffId, fullName, staffIds, page, pageSize, orderBy, isPagingEnabled },
  });



