import { request } from 'utils';

const prefix = '/api/usermanagement';

export const searchUserPortal = ({
  userIds,
  userTypeId,
  userRelatedIds,
  page,
  pageSize = 10,
  orderBy,
  isPagingEnabled = true,
} = {}) =>
  request({
    prefix,
    url: `/UserPortal/SearchUserPortal`,
    method: 'POST',
    params: {},
    data: { userIds, userTypeId, userRelatedIds, page, pageSize, orderBy, isPagingEnabled },
  });

export const massCreateStudentAccount = data =>
  request({
    prefix,
    url: `/UserPortal/MassCreateStudentAccount`,
    method: 'POST',
    params: {},
    data,
  });

export const massCreateStaffAccount = data =>
  request({
    prefix,
    url: `/UserPortal/MassCreateStaffAccount`,
    method: 'POST',
    params: {},
    data,
  });

export const updatePassword = ({ userId, password } = {}) =>
  request({
    prefix,
    url: `/UserPortal/${userId}/UpdatePassword`,
    method: 'PUT',
    params: {},
    data: { password },
  });

export const updateStatus = ({ userId, disabled } = {}) =>
  request({
    prefix,
    url: `/UserPortal/${userId}/UpdateStatus`,
    method: 'PUT',
    params: {},
    data: { disabled },
  });



